import React, {Component} from 'react';
import './Css/Login.css'
class TodoItem extends Component{
    render(){
        let TodoList="";
        if(this.props.item.isListened){
            TodoList = "TodoList";
        }
        return (
            <div className = {TodoList}>
                <p>{this.props.item.sing}</p>
            </div>
        );
    }
}
export default TodoItem;