import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import TodoItem from './components/TodoItem';
import HeaderLogin from './components/HeaderLogin';
import LoginForm from './components/LoginForm';
import MenuIcon from './components/MenuIcon';
import TrafficLight from './components/TrafficLight';

const RED = 0;
const ORANGE = 1;
const GREEN = 2;

class App extends Component {
  constructor(){
    super();

    this.items = [
      {sing: 'Sao Em Vô Tình', isListened: true},
      {sing: 'Về Bên Anh', isListened: true},
      {sing: 'Sóng Gió', isListened: false}
    ];

    this.menu = [
      {name: "HOME", icon: "zmdi zmdi-home"},
      {name: "DEALS", icon: "zmdi zmdi-card-giftcard"},
      {name: "UPLOAD", icon: "zmdi zmdi-coffee"},
      {name: "SETTINGS", icon: "zmdi zmdi-settings"}
    ];
  
    this.state = {
      currentColor: RED
    };
    
    setInterval(() =>{
      this.setState({
         currentColor: this.getNextColor(this.state.currentColor)
        });
    }, 1000);

  }

  getNextColor(color){
    switch(color){
      case RED: return ORANGE;
      case ORANGE: return GREEN;
      default: return RED;
    }
  }


  render() {
    const {currentColor} = this.state;
    return (
      <div className="App">
      <HeaderLogin />
      <LoginForm />
      <TrafficLight currentColor={currentColor}/>
      <div>
        {this.menu.map((content, index) => <MenuIcon key = {index} item= {content} />)};
      </div>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <div>
            {this.items.map((item, index) => <TodoItem key = {index} item = {item} />)}
          </div>
        </header>
      </div>
    );
  }
}

export default App;
